.form {
    max-width: 960px;
    margin: 0 auto;
}

.form-form {
    max-width: 480px;
    margin: 50px 0 0 0;
}

.form-field {
    display: flex;
    flex-direction: column;
    margin: 0 0 20px 0
}

.form-field label {
    font-weight: bold;
    margin: 0 0 10px;
}

.form-field input {
    font-family: "Quicksand", sans-serif;
    padding: 10px;
    border: none;
    border-radius: 5px;
    box-shadow: inset 0 0 5px #00000050;
    font-weight: bold;
    color: var(--green)
}

.form-field select {
    font-family: "Quicksand", sans-serif;
    padding: 10px;
    border: none;
    border-radius: 5px;
    box-shadow: inset 0 0 5px #00000050;
    font-weight: bold;
    color: var(--green)
}

@media screen and (max-width: 960px) {
    .form {
        width: 90vw;
        margin: 0 auto;
    }
    .form-form {
        max-width: unset;
    }
}