.nav {
    display: flex;
    align-items: center;
    justify-content: center;
}

.nav a {
    margin: 20px 50px;
    color: var(--green);
    text-decoration: none;
    transition: ease-in-out .1s;
}

.nav a:hover {
    color: white
}

.nav a.demo {
    color: white;
    background-color: var(--green);
    padding: 10px;
    border-radius: 5px;
}

.nav a.demo:hover {
    color: var(--green);
    background-color: white;
}

.burger-menu {
    display: none;
    font-size: 24px;
    cursor: pointer;
}

.menu-items {
    display: flex;
    align-items: center;
}

.menu-items a {
    margin: 20px 50px;
    color: var(--green);
    text-decoration: none;
    transition: ease-in-out .1s;
}

.menu-items a:hover {
    color: white;
}


/* Afficher le menu sur les petits Ã©crans */

@media screen and (max-width: 960px) {
    .nav a {
        margin: 20px 50px;
    }
    .nav {
        justify-content: space-between;
    }
    .burger-menu {
        display: block;
        margin: 0 50px 0 0;
    }
    .menu-items {
        display: none;
        flex-direction: column;
        background-color: #f9f9f9;
        position: absolute;
        top: 80px;
        left: 0;
        width: 100%;
        padding: 10px;
        box-shadow: 0 2px 5px rgba(0, 0, 0, 0.2);
    }
    .menu-items.show {
        display: flex;
    }
    .menu-items a {
        margin: 10px 0;
    }
}