.reactMarkdown h1 {
    margin: 0 0 20px 0;
}

.reactMarkdown h2 {
    margin: 0 0 20px 0;
}

.reactMarkdown pre {
    padding: 0 0 0 20px;
    border-left: solid 1px var(--green);
    margin: 0 0 20px 0;
    overflow: scroll;
}

.reactMarkdown ol {
    padding: 0 0 0 20px;
}

.reactMarkdown ul {
    list-style-type: square;
    padding: 0 0 0 20px;
}

.reactMarkdown li {
    margin: 0 0 20px 0;
}