@import url('https://fonts.googleapis.com/css2?family=Quicksand:wght@300..700&display=swap');
:root {
    --orange: #ffc070;
    --green: #00856c;
    --dark-orange: #f7823c;
    --red: #850d00;
    --pale-orange: #ffe9d6;
    --backgroud-gradient: linear-gradient(to right, #ffcc79, #ffa050);
    background: var(--backgroud-gradient);
    color: var(--green);
    font-family: "Quicksand", sans-serif;
}

* {
    margin: 0;
    padding: 0;
}