.user-profile,
.no-info {
    max-width: 960px;
    margin: 0 auto;
}

.user-profile p {
    margin: 20px 0;
}

.user-profile .info-name {
    font-weight: bold;
}

.user-profile-info {
    background-color: white;
    padding: 20px;
    border-radius: 5px;
}

.user-timelines-catalog .timeline-preview {
    box-shadow: 0 0 10px #00000050;
    width: 48%;
    min-width: 250px;
}

.user-timelines-catalog {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
}

.user-completed-roadmaps .roadmap-preview {
    box-shadow: 0 0 10px #00000050;
    width: 48%;
    min-width: 250px;
}

.user-completed-roadmaps {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
}