.roadmap-preview {
    /* width: 40%; */
    max-width: 460px;
    min-width: 200px;
    /* margin: 2.5% 2.5%; */
    margin: 20px 0;
    color: var(--green);
    background-color: white;
    border-radius: 5px;
    /* display: flex; */
    align-items: center;
    justify-content: center;
    position: relative;
    transition: ease-in-out .1s;
    padding: 0 0 40px 0;
}

.roadmap-preview:hover {
    color: white;
    background-color: var(--green);
}

.roadmap-preview:hover .roadmap-preview-see-more {
    color: white
}

.roadmap-preview h3 {
    padding: 0 20px;
    margin: 20px 0 0 0;
}

.roadmap-preview p {
    padding: 0 20px;
    margin: 20px 0 0 0;
}

.roadmap-preview a {
    text-decoration: none;
}

.roadmap-preview-see-more {
    position: absolute;
    bottom: 10px;
    right: 10px;
    color: var(--green);
}

.roadmap-preview-see-more:hover {
    text-decoration: underline;
}

@media screen and (max-width: 960px) {
    .roadmap-preview {
        width: 90vw;
        margin: 20px auto;
    }
}