/* GENERAL */

.roadmap-module {
    /* max-width: 960px; */
    background-color: white;
    padding: 20px;
    margin: 10px 0 50px 0;
    border-radius: 5px;
}

.roadmap-module h3 {
    margin: 0 0 20px 0;
}

.roadmap-module p {
    margin: 0 0 20px 0;
}

.roadmap-module-ndlr {
    font-style: italic;
}


/* MEETING */

.roadmap-module-expert {
    margin: 0 0 0 20px;
}


/* PODCAST */

.roadmap-module-podcast-link {
    margin: 0 0 20px 0;
}

.roadmap-module-podcast a {
    color: var(--green);
}

.roadmap-module-podcast-transcription {
    max-height: 300px;
    overflow: auto;
    padding: 20px 20px 20px 20px;
    box-shadow: inset 0 0 10px #00000050;
    border-radius: 5px;
}

.roadmap-module-podcast-seemore {
    text-decoration: underline;
}

.roadmap-module-podcast-metadata {
    display: flex;
    align-items: center;
    justify-content: space-between;
}


/* REMINDER */

.roadmap-module-reminder p {
    margin: 0;
}


/* TO DO */

.roadmap-module-to-do-to-do li {
    display: flex;
    align-items: baseline;
}

.roadmap-module-to-do-to-do li p {
    margin: 0 0 20px 10px;
}

.roadmap-module-to-do-to-do li .roadmap-module-to-do-check {
    margin: 0 0 0 20px;
}

.roadmap-module-to-do-to-do li:last-of-type p {
    margin: 0 0 0 10px;
}


/* WEBINAR */

.roadmap-module-webinar-image {
    padding-top: 320px;
    position: relative;
}

.roadmap-module-webinar-no-image {
    position: relative
}

.roadmap-module-webinar-img-container {
    position: absolute;
    overflow: hidden;
    width: 100%;
    top: 0;
    left: 0;
    height: 300px;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 5px 5px 0 0;
}

.roadmap-module-webinar-img-container img {
    width: 100%;
    height: 100%;
    object-fit: cover;
}