.footer {
    background-color: var(--pale-orange);
    margin: 50px 0 0 0;
    padding: 50px 0 100px 0;
    box-shadow: 0 0 15px #00000050;
}


/* UPPER PART */

.footer .upper-part {
    max-width: 1080px;
    margin: 50px auto;
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr;
}

.footer .upper-part div {
    padding: 10px;
}

.footer a {
    color: var(--green);
    text-decoration: none;
}

.footer a:hover {
    text-decoration: underline;
}

.footer .title {
    grid-column: 1;
    grid-row: 1;
    font-size: larger;
    font-weight: bold;
}

.footer .solution {
    grid-column: 2;
    grid-row: 1;
}

.footer .team {
    grid-column: 2;
    grid-row: 2;
}

.footer .us {
    grid-column: 3;
    grid-row: 1;
    font-weight: bold;
}

.footer .email {
    grid-column: 3;
    grid-row: 2;
}

.footer .tel {
    grid-column: 3;
    grid-row: 3;
}

.footer .coffee {
    grid-column: 3;
    grid-row: 4;
}

.footer .linkedin {
    grid-column: 3;
    grid-row: 5;
}

.footer .newsletter {
    grid-column: 4;
    grid-row: 1;
    font-weight: bold;
}

.footer .newsletter-content {
    grid-column: 4;
    grid-row: 2/6;
}

.footer .newsletter-subscription {
    margin: 20px 0;
}


/* HR */

.footer hr {
    max-width: 1080px;
    margin: 0 auto;
    border: solid 2px var(--green)
}


/* LOWER PART */

.footer .lower-part {
    max-width: 1080px;
    margin: 50px auto;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-around;
}


/* COPYRIGHT PART */

.footer .copyright-part {
    max-width: 1080px;
    margin: 50px auto;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    font-weight: bold;
    text-align: center;
}

.footer .copyright-part a {
    text-decoration: underline;
}

@media screen and (max-width: 960px) {
    .footer .upper-part {
        width: 90vw;
        margin: 50px auto;
        grid-template-columns: 1fr;
    }
    .footer .upper-part div {
        grid-column: auto;
        grid-row: auto;
        text-align: center;
    }
    .footer .lower-part {
        width: 90vw;
        margin: 50px auto;
    }
    .footer .copyright-part {
        width: 90vw;
        margin: 50px auto;
    }
}