button {
    color: white;
    background-color: var(--green);
    border: none;
    padding: 10px;
    border-radius: 5px;
    transition: ease-in-out .1s;
    font-family: "Quicksand", sans-serif;
    font-size: 16px;
}

button:hover {
    color: var(--green);
    background-color: white;
}

.form-submit {
    color: white;
    background-color: var(--green);
    border: none;
    padding: 10px;
    border-radius: 5px;
    transition: ease-in-out .1s;
    font-family: "Quicksand", sans-serif;
    font-size: 16px;
}

.form-submit:hover {
    color: var(--green);
    background-color: white;
}

.sign-out:hover {
    background-color: var(--red);
    color: white
}

.go-back {
    margin: 0 0 50px 0;
}

.newsletter-subscription a {
    color: white !important;
    background-color: var(--green);
    border: none;
    padding: 10px;
    border-radius: 5px;
    transition: ease-in-out .1s;
    font-family: "Quicksand", sans-serif;
    font-size: 16px;
}

.newsletter-subscription a:hover {
    color: var(--green) !important;
    background-color: white;
    text-decoration: none !important;
}

@media screen and (max-width: 960px) {
    .go-back {
        margin-left: 50%;
        transform: translateX(-50%);
    }
    button {
        margin-left: 50%;
        transform: translateX(-50%);
    }
    .form-submit {
        margin-left: 50%;
        transform: translateX(-50%);
    }
}